@large: 1198.98px;
@medium: 990.98px;
@small: 767.98px;

body {
  .proximaRegular;

  color: @dark-blue;

  font-size: 20px;
}

#mobile-nav-toggle {
  position: absolute;
  left: -100%;
  top: -100%;
}

.header {
  min-height: 100vh;
  color: @white;
  background-color: @dark-blue;

  padding-top: 33px;

  position: relative;
  overflow: hidden;
  &.small {
    height: auto;
    min-height: auto;
  }

  a {
    color: white;
  }

  .logo {
    background-image: data-uri('../images/buystep-logo.svg');
    display: inline-block;
    width: 200px;
    height: 58px;

    background-position: center;
    background-repeat: no-repeat;

    @media screen and (max-width: @medium) {
      width: 180px;
    }
  }

  .links {
    float: right;
    line-height: 80px;

    a {
      margin-right: 65px;

      &:last-child {
        margin-right: 0;
      }
    }
    .button{
      display: inline;
    }

    @media screen and (max-width: @large) {
      a {
        margin-right: 50px;
      }
    }

    @media screen and (max-width: @medium) {
      a:not(.button) {
        margin-right: 20px;
        font-size: 18px;

        white-space: nowrap;
      }
    }
  }

  .main-screen {

    padding-top: 18vh;

    h1 {
      margin: 0;
      .proximaSemi;
      font-size: 52px;
      line-height: 56px;
    }

    @media screen and (max-height: 700px) {
      padding-top: 14vh;
    }

    .description {
      margin-top: 25px;
      margin-bottom: 70px;
      line-height: 28px;
      p{
        margin: 0 0 10px;
        padding: 0;
      }
    }

    @media screen and (max-width: @large) {
      .button {
        white-space: nowrap;
      }
    }


    .video-link {

      &:hover {
        color: @lemon;
      }

      .icon-video {
        margin-right: 10px;
      }
    }
  }

  .phone {
    background: url('../images/phone.png') no-repeat top center;
    background-size: 100%;
    margin: 50px auto 0;
    width: 100%;
    height: 60vh;
    top: 0;
    left: 0;
    position: relative;
    &:after {
      display: none;
      content: '';
      position: absolute;
      bottom: 0;
      left: 30px;
      width: 100%;
      height: 100%;
      background: data-uri('../images/hand.svg') no-repeat left bottom;
    }

    @media screen and (min-width: 400px) {
      width: 380px;
      height: 360px;
    }

    @media screen and (min-width: 992px) {
      height: 652px;
      background-position: top left;
      background-size: contain;
      &:after{
        display: block;
      }
    }




    // @media screen and (max-height: 850px) {
    //   top: 40px;
    //   height: 564px;

    //   &:after {
    //     height: 392px;
    //   }
    // }

    // @media screen and (max-height: 700px) {
    //   top: 25px;
    //   height: 539px;

    //   &:after {
    //     height: 374px;
    //     right: -50px;
    //   }
    // }

  }


  .mouse-icon {
    display: block;
    background-position: center;
    background-repeat: no-repeat;
    background-image: data-uri('../images/mouse.svg');
    width: 30px;
    height: 42px;

    position: absolute;

    bottom: 45px;
    left: 50%;
    margin-left: -15px;

    &:after {
      content: '';
      position: absolute;
      width: 100%;
      bottom: -20px;
      height: 8px;
      background: data-uri('../images/chevron.svg') no-repeat center;
    }
  }
}

.onboarding {
  position: relative;

  margin: 150px 0;

  .obd-screen {
    min-height: 525px;
    background-repeat: no-repeat;
    background-size: auto 400px;

    h2 {
      .proximaSemi;
      font-size: 45px;
      margin-top: 0;
      margin-bottom: 15px;
    }

    p {
      line-height: 1.5;
      margin: 15px 0;
    }

    &.how-to-start {
      background-image: url('../images/onboarding/slide-1.svg');
      background-position: ~"calc(50% - 250px)" bottom;
    }

    &.rise-up {
      background-image: url('../images/onboarding/slide-2.svg');
      background-position: ~"calc(50% - 300px)" bottom;
    }

    &.get-discount {
      background-image: url('../images/onboarding/slide-3.svg');
      background-position: ~"calc(50% - 300px)" bottom;
    }

    &.choose {
      background-image: url('../images/onboarding/slide-4.svg');
      background-position: ~"calc(50% - 300px)" bottom;
    }

    &.max-discount {
      background-image: url('../images/onboarding/slide-5.svg');
      background-position: ~"calc(50% - 300px)" bottom;
    }
  }

  .onboarding-controls {
    position: absolute;
    width: 100%;
    bottom: 250px;

    .app-links {
      width: 60%;

      display: flex;

      a:first-child {
        margin-right: 15px;
      }
    }

    .buttons {
      width: 20%;
      display: flex;

      a {
        display: block;
        width: 47%;
        height: 0;
        padding-top: 46.5%;
        border-radius: 50%;

        &.arr-prev {
          margin-right: 6%;
        }
      }
    }

    .buttons-dots {
      display: none;

      margin: 20px 0 30px;

      .mini-dot {
        display: inline-block;
        cursor: pointer;
        width: 10px;
        height: 10px;
        padding: 0;
        border: 0 none;
        background-color: @slider-dot;

        margin-right: 15px;

        &.active {
          background-color: @slider-dot-active;
        }

        &:last-child {
          margin-right: 0;
        }
      }

    }
  }
}

.advantages {
  margin: 140px 0;

  h4 {
    .proximaSemi;
    font-size: 24px;

    max-width: 260px;
    margin: 35px auto 15px;
  }

  p {
    max-width: 300px;
    margin: 0 auto;
    line-height: 1.4;
  }

  .card {
    display: inline-block;
    width: 120px;
    height: 120px;

    border-radius: 40px;

    box-shadow: 0 0 50px 0 fade(@deep-blue, 20%);

    background-position: center;
    background-repeat: no-repeat;

    -moz-user-select: none;
    user-select: none;

    &.innovation {
      background-image: data-uri('../images/adv/cup.svg');
    }

    &.economy {
      background-image: data-uri('../images/adv/wallet.svg');
    }

    &.activity {
      background-image: data-uri('../images/adv/sneaker.svg');
    }
  }
}

.partners {
  margin: 130px 0 130px;

  h3 {
    .proximaSemi;
    font-size: 45px;

    margin-top: 5px;
    margin-bottom: 25px;
  }
}

.footer {
  background-color: @footer-bgr;
  color: @white;
  font-size: 18px;
  .top {
    margin-bottom: 20px;
  }

  .articles {
    .wrapper {
      border-bottom: 1px solid fade(@line-col, 25%);

      padding: 20px 0;

      a {
        padding: 20px 0;

        width: 100%;
        display: inline-block;
        text-align: center;

        img {
          vertical-align: middle;
          opacity: 0.5;

          transition-duration: 0.2s;
          max-height: 50px;
          max-width: 100%;
        }

        span {
          color: white;
        }

        &:hover {
          img {
            opacity: 1;
          }
        }
      }
    }
  }

  h5 {
    font-size: 20px;
    margin-top: 55px;
    margin-bottom: 30px;
  }

  .common-links {
    list-style: none;
    padding-left: 0;

    li {
      padding-bottom: 15px;

      a {
        color: @white;
      }
    }
  }

  .two-level-menu {
    padding-bottom: 25px;

    .sub-head {
      color: @footer-lbl;
      display: block;
    }

    a {
      color: @white;
    }
  }

  .footer-logo {
    margin: 30px 0;

    background-image: data-uri('../images/buystep-logo.svg');
    display: inline-block;
    width: 160px;
    height: 46px;
  }

  .footer-contacts {
    a {
      color: @white;
      display: block;
    }

    margin-bottom: 25px;
  }

  .footer-links {
    a {
      text-indent: -9999px;

      display: inline-block;

      width: 44px;
      height: 44px;

      border: 1px solid fade(@white, 50%);
      border-radius: 50%;

      transition-duration: .2s;
      transition-property: border-color;

      margin-right: 25px;

      .icon {
        background-position: center;
        background-repeat: no-repeat;
      }

      &.icon-vk {
        background-image: data-uri('../images/icons/icon-vk.svg');
      }

      &.icon-instagram {
        background-image: data-uri('../images/icons/icon-instagram.svg');
      }

      &.icon-facebook {
        background-image: data-uri('../images/icons/icon-facebook.svg');
      }

      &:hover {
        border-color: @white;
      }
    }
  }


  .copy {
    color: @footer-lbl;
    background-color: @dark-blue;

    p {
      font-size: 18px;
      margin: 35px 0 40px;
    }

    .app-links {
      margin: 25px 0;

      a {
        margin-right: 15px;
      }
    }
  }
}

.antoxa-wrap {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: fadeOut(#000, 20%);
  text-align: center;

  img {
    max-height: 100vh;
    max-width: 100%;
  }
}

@media screen and (max-width: @small) {
  .header {

    #mobile-nav-toggle {
      &:focus ~ .toggle-container .button-toggle {
        box-shadow: 0 0 0 8px rgba(0, 0, 0, 0.1), inset 0 0 0 20px rgba(0, 0, 0, 0.1);
      }

      &:checked {
        ~ .toggle-container {
          .button-toggle {
            box-shadow: 0 0 0 550px rgba(0, 0, 0, 0.1), inset 0 0 0 20px rgba(0, 0, 0, 0.1);

            &:hover {
              box-shadow: 0 0 0 550px rgba(0, 0, 0, 0.1), inset 0 0 0 20px rgba(0, 0, 0, 0.1), 0 0 0 8px rgba(0, 0, 0, 0.1), inset 0 0 0 20px rgba(0, 0, 0, 0.1);
            }

            &:before {
              -webkit-transform: translateY(-50%) rotate(45deg) scale(1);
                  -ms-transform: translateY(-50%) rotate(45deg) scale(1);
                      transform: translateY(-50%) rotate(45deg) scale(1);
            }

            &:after {
              -webkit-transform: translateY(-50%) rotate(-45deg) scale(1);
                  -ms-transform: translateY(-50%) rotate(-45deg) scale(1);
                      transform: translateY(-50%) rotate(-45deg) scale(1);
            }
          }
        }

        &:focus ~ .toggle-container .button-toggle {
          box-shadow: 0 0 0 550px rgba(0, 0, 0, 0.1), inset 0 0 0 20px rgba(0, 0, 0, 0.1), 0 0 0 8px rgba(0, 0, 0, 0.1), inset 0 0 0 20px rgba(0, 0, 0, 0.1);
        }
      }
    }


    .button-toggle {
      position: absolute;
      display: inline-block;
      width: 20px;
      height: 20px;
      margin: 25px;
      background-color: transparent;
      border: none;
      cursor: pointer;
      border-radius: 100%;
      -webkit-transition: 0.6s;
      transition: 0.6s;
    }

    .button-toggle:hover {
      box-shadow: 0 0 0 8px rgba(0, 0, 0, 0.1), inset 0 0 0 20px rgba(0, 0, 0, 0.1);
    }

    .button-toggle:before, .button-toggle:after {
      position: absolute;
      content: '';
      top: 50%;
      left: 0;
      width: 100%;
      height: 2px;
      background-color: @white;
      border-radius: 5px;
      -webkit-transition: 0.5s;
      transition: 0.5s;
    }

    .button-toggle:before {
      -webkit-transform: translateY(-50%) rotate(45deg) scale(0);
          -ms-transform: translateY(-50%) rotate(45deg) scale(0);
              transform: translateY(-50%) rotate(45deg) scale(0);
    }

    .button-toggle:after {
      -webkit-transform: translateY(-50%) rotate(-45deg) scale(0);
          -ms-transform: translateY(-50%) rotate(-45deg) scale(0);
              transform: translateY(-50%) rotate(-45deg) scale(0);
    }

    .nav {
      display: inline-block;
      margin: 25px 25px 20px;
      pointer-events: none;
      -webkit-transition: 0.5s;
      tranition: 0.5s;
    }

    .logo-wrap {
      text-align: center;
    }

  }
}
