.visible-xs {
  display: none;
}

.mobile-menu {
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 500;
  background: fade(black, 80%);
  padding: 15px;
  &.js-show {
    display: block;
  }
  .close {
    position: absolute;
    top: 30px;
    right: 15px;
    width: 20px;
    height: 20px;
    background: data-uri('../images/close.svg') center no-repeat;
    background-size: contain;
    border: none;
  }
  .links {
    width: 100%;
    max-width: 540px;
    margin: 0 auto;
    padding-top: 20vh;
    a {
      color: white;
      display: block;
      padding: 12px;
      font-size: 24px;
    }
    .button {
      text-align: center;
      margin-top: 20px;
    }
  }
}

.mobile-menu-burger {
  display: none;
  position: absolute;
  top: 20px;
  left: 25px;
  background: data-uri('../images/burger.svg') center no-repeat;
  background-size: contain;
  width: 20px;
  height: 20px;
  z-index: 100;
  border: none;
}

@media screen and (max-width: 1199px) {
  header {
    .container {
      position: relative;
    }
  }
  // .header {
  //   .phone {
  //     left: 0;
  //     margin: 0 auto;
  //   }
  // }
  .partners-container {
    .slider {
      .slide {
        width: 30%;
        img {
          width: 100%;
        }
      }
    }
  }
  .footer {
    footer {
      .articles {
        overflow: hidden;
        +.row {
          border-top: 1px solid fade(@line-col, 25%);
        }
        >.col-12 {
          padding: 0;
        }
        .wrapper {
          overflow-x: visible;
          overflow-y: hidden;
          padding: 0 40px 0 15px;
          position: relative;
          top: 20px;
          a {
            padding: 20px 20px 50px 20px;
            &:first-child {
              padding-left: 0;
            }
          }
        }
      }
    }
    .copy {
      .app-links {
        text-align: right;
        a {
          color: transparent;
          font-size: 0;
          vertical-align: bottom;
          display: inline-block;
          margin-bottom: 15px;
        }
      }
    }
  }
}

@media screen and (max-width: 991px) {
  // .header {
  //   .phone {
  //     background-size: cover;
  //     background-position: top;
  //     height: 440px;
  //     &:after {
  //       display: none;
  //     }
  //   }
  // }
  .onboarding {
    .onboarding-controls {
      bottom: 100px;
    }
  }
  .partners-container {
    .slider {
      .slide {
        overflow: initial;
        .info {
          top: 100%;
          color: black;
          padding-top: 20px;
        }
      }
    }
  }
  .footer {
    .bottom {
      text-align: center;
      .app-links {
        text-align: center;
        a {
          width: 43%;
        }
      }
      &.copy {
        padding-top: 15px;
        p {
          margin-top: 0;
        }
        .container {
          .row {
            flex-direction: column-reverse;
          }
        }
      }
    }
  }
}

@media screen and (max-width: 767px) {
  .visible-xs {
    display: block;
  }
  .onboarding {
    .obd-screen {
      background-size: auto 270px !important;
      background-position: top center !important;
      h2 {
        margin-top: 270px;
        font-size: 35px;
      }
    }
    .onboarding-controls {
      bottom: -50px;
      .col-md-6 {
        flex-direction: column-reverse;
      }

      .buttons-dots {
        display: block;
      }

      .buttons {
        display: none;
        width: 40%;
      }
      .app-links {
        width: 80%;
        justify-content: center;
      }
    }
  }
  .footer {
    h5 {
      margin-top: 23px;
    }
    .copy {
      text-align: left;
      padding: 0 10px;
    }
  }
  .show-submenu-action {
    display: block;
    position: relative;
    cursor: pointer;
    padding-left: 10px;
    &:after {
      content: '';
      position: absolute;
      right: 10px;
      top: 0;
      background: data-uri('../../public/images/chevron.svg') center no-repeat;
      background-size: contain;
      width: 14px;
      height: 24px;
    }
    &.js-show {
      &:after {
        transform: scaleY(-1);
      }
      +.show-submenu {
        max-height: 260px;
      }
    }
    +.show-submenu {
      overflow: hidden;
      max-height: 0;
      transition: all 150ms linear;
      padding-left: 10px;
    }
  }
  .advantages {
    .card {
      margin-top: 50px;
    }
  }
  .mobile-menu-burger {
    display: block;
  }
}

@media screen and (max-width: 575px) {
  body {
    font-size: 18px;
  }
  .text-center-xs {
    text-align: center;
  }
  .partners-container {
    .slider {
      .slide {
        width: 100%;
      }
    }
  }
  .header {
    min-height: 90vh;
    .main-screen {
      padding-top: 20px;
    }
    .logo {
      width: 137px;
    }
    // .phone {
    //   width: 100%;
    //   height: 43vh;
    //   margin: 30px auto 0 -17px;
    //   position: static;
    // }
    .main-screen {
      h1 {
        font-size: 32px;
        line-height: 1.3;
        color: @lemon;
        text-align: center;
      }
      .description {
        padding: 0 10px;
      }
    }
  }
  .advantages {
    h4 {
      font-size: 23px;
    }
  }
  .partners {
    h3 {
      font-size: 35px;
      text-align: left;
    }
  }
}

@media screen and (max-width: 450px) {
  .onboarding {
    .onboarding-controls {
      bottom: -100px;
    }
  }
}
