// 03-controls.less

.button {
  border: 1px solid fadeout(@white, 50%);
  border-radius: 10px;
  display: inline-block;
  .proximaSemi;

  padding: 15px 35px;

  @media screen and (max-width: 991px) {
    padding: 12px 30px;
  }

  transition-duration: 0.2s;

  &:hover {
    border-color: @white;
    text-decoration: none;
  }

  &.lemon {
    background-color: @lemon;
    border-color: @lemon;
    color: @dark-blue;

    &:hover {
      box-shadow: 0 0 30px 0 fade(@lemon, 50%);
    }
  }

  &.round-lemon-arr {
    width: 56px;
    height: 56px;
    padding: 0;

    // transition-duration: 0s;

    border: 0 none;
    display: inline-block;

    text-indent: -9999px;

    cursor: pointer;

    background-repeat: no-repeat;
    background-position: center;
    background-image: data-uri('../images/arr.svg');

    overflow: hidden;

    &.arr-next {
      transform: rotate(180deg);
    }

    &.arr-prev {
      margin-right: 20px;
    }

    &:hover {
      border-radius: 56px;
      background-color: @lemon;
    }
  }

  &.empty {
    .proximaRegular;
    border: 0 none;
  }
}

.icon-video {
  display: inline-block;
  width: 26px;
  height: 26px;

  vertical-align: -0.325em;

  background: data-uri('../images/icon-video.svg') no-repeat center;
}

.mobile-market {
  display: inline-block;
  width: 10.125rem;
  height: 3rem;

  text-indent: -9999px;

  background-position: center;
  background-repeat: no-repeat;
}

.appstore {
  .mobile-market;

  background-image: data-uri('../images/appstore.svg');
}

.google-play {
  .mobile-market;

  background-image: data-uri('../images/google-play.svg');
}
