.partners {

  #partners-carousel {
    width: 100%;
  }

  .carousel-3d-container {
    min-height: 1px;
    width: 100%;
    position: relative;
    z-index: 0;
    /*overflow: hidden;*/
    margin: 20px auto;
    /*box-sizing: border-box;*/
    padding: 20px 0;
  }

  .carousel-3d-slider {
    position: relative;
    margin: 0 auto;
    transform-style: preserve-3d;
    -webkit-perspective: 1000px;
    -moz-perspective: 1000px;
    perspective: 1000px;
  }

  .carousel-3d-slide {
    position: absolute;
    opacity: 0;
    visibility: hidden;
    overflow: hidden;
    top: 0;
    background-size: cover;
    background-color: #ccc;
    display: block;
    margin: 0;
    box-sizing: border-box;

    border-radius: 12px;
    box-shadow: 0 0 25px 0 rgba(0, 0, 0, 0.5);

    text-align: left;

    img {
      width: 100%;
    }

    &.current {
      opacity: 1 !important;
      visibility: visible !important;
      transform: none !important;
      z-index: 999;

      transform: scale(1.2) !important;
    }

    img {
      vertical-align: middle;
    }

    .info {
      position: absolute;

      color: @white;

      bottom: 33px;
      left: 0;
      right: 0;
      padding: 0 50px;
      text-align: center;

      p {
        font-size: 16px;
        margin: 0;
      }

      .title {
        .proximaSemi;
        font-size: 20px;

        &:before {
          content: '';
        }
        &:after {
          content: '';
        }
      }
    }

    .discount {
      position: absolute;

      bottom: 7px;
      right: 7px;

      width: 85px;
      height: 60px;

      // background-color: @lemon;
      background: data-uri('../images/discount-bg.svg') no-repeat center;

      border-bottom-right-radius: 10px;

      span {
        position: absolute;
        .proximaSemi;
        top: 10px;
        right: 13px;

        .proximaSemi;
        font-size: 15px;

        &.percent {
          top: 25px;
          font-size: 20px;
          .proximaSemi;
        }
      }
    }
  }
}
