// 02-typography.less

@font-face {
  font-family: 'Proxima Nova Rg';
  src: url('../fonts/proximanova/font/ProximaNova-Regular.eot');
  src: local('Proxima Nova Regular'), local('ProximaNova-Regular'),
    url('../fonts/proximanova/font/ProximaNova-Regular.eot?#iefix') format('embedded-opentype'),
    url('../fonts/proximanova/font/ProximaNova-Regular.woff') format('woff'),
    url('../fonts/proximanova/font/ProximaNova-Regular.ttf') format('truetype');
  font-style: normal;
  font-weight: normal;
}

@font-face {
  font-family: 'Proxima Nova Lt';
  src: url('../fonts/proximanova/font/ProximaNova-Semibold.eot');
  src: local('Proxima Nova Semibold'), local('ProximaNova-Semibold'),
    url('../fonts/proximanova/font/ProximaNova-Semibold.eot?#iefix') format('embedded-opentype'),
    url('../fonts/proximanova/font/ProximaNova-Semibold.woff') format('woff'),
    url('../fonts/proximanova/font/ProximaNova-Semibold.ttf') format('truetype');
  font-style: normal;
  font-weight: normal;
}

// snippets
.proximaRegular {
  font-family: 'Proxima Nova Rg', sans-serif;
}

.proximaSemi {
  font-family: 'Proxima Nova Lt', sans-serif;
  font-weight: 400;
}
